<template>
  <v-container>
    <!-- Arbeitserfahrung  -->
    <v-card class="overflow-hidden" elevation="0" color="background">
      <v-app-bar
        :collapse="!collapseOnScroll"
        absolute
        color="secondary"
        dark
        scroll-target="#scrolling-techniques-6"
      >
        <v-icon>mdi-briefcase</v-icon>
        <v-spacer></v-spacer>
        <v-toolbar-title>Arbeitserfahrung</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-card-actions>
          <v-btn color="accent" x-small depressed @click="handleClick">
            <div v-if="!collapseOnScroll">
              <v-icon small>mdi-arrow-right</v-icon>
            </div>
            <div v-else>
              <v-icon small>mdi-arrow-left</v-icon>
            </div>
          </v-btn>
        </v-card-actions>
      </v-app-bar>
      <v-sheet
        id="scrolling-techniques-6"
        class="overflow-y-auto"
        min-height="75"
      >
        <v-container style="height: auto"></v-container>
      </v-sheet>
      <v-expand-transition>
        <div v-show="collapseOnScroll">
          <v-timeline align-top dense>
            <v-timeline-item
              color="accent"
              small
              fill-dot
              v-for="(data, i) in timelineData"
              :key="i"
            >
              <div v-bind="{ information }">
                <v-row class="pt-1">
                  <v-col cols="3">
                    <div class="caption">
                      <strong>{{ data.period }}</strong>
                    </div>
                  </v-col>
                  <v-col>
                    <strong>{{ data.job }}</strong>
                    <v-tooltip
                      v-model="show"
                      bottom
                      color="secondary"
                      max-width="400"
                      close-delay="200"
                    >
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn
                          icon
                          color="background"
                          x-small
                          depressed
                          v-bind="attrs"
                          v-on="on"
                          v-if="data.details"
                        >
                          <v-icon color="secondary">mdi-information</v-icon>
                        </v-btn>
                      </template>

                      <v-card
                        color="secondary"
                        v-for="(detail, j) in data.details"
                        :key="j"
                      >
                        <v-list flat color="secondary">
                          <v-subheader class="accent--text">{{
                            detail.task
                          }}</v-subheader>
                          <v-list-item-group>
                            <v-list-item
                              v-for="(subtask, i) in detail.subtasks"
                              :key="i"
                            >
                              <v-list-item-content>
                                <div class="caption white--text">
                                  - {{ subtask }}
                                </div>
                              </v-list-item-content>
                            </v-list-item>
                          </v-list-item-group>
                        </v-list>
                      </v-card>
                    </v-tooltip>

                    <div class="caption">
                      <a v-bind:href="data.link">{{ data.company }}</a>
                    </div>
                    <!-- v-chips -->
                    <v-chip
                      small
                      outlined
                      v-for="(elem, j) in data.chips"
                      :key="j"
                    >
                      {{ elem }}
                    </v-chip>
                  </v-col>
                </v-row>
              </div>
            </v-timeline-item>
          </v-timeline>
        </div>
      </v-expand-transition>
    </v-card>
  </v-container>
</template>

<script>
export default {
  data: () => ({
