<template>
  <v-container>
    <!-- Skills  -->
    <v-card class="overflow-hidden" elevation="0" color="background">
      <v-app-bar
        :collapse="!collapseOnScroll"
        absolute
        color="secondary"
        dark
        scroll-target="#scrolling-techniques-6"
      >
        <v-icon>mdi-flash</v-icon>
        <v-spacer></v-spacer>
        <v-toolbar-title>Skills</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-card-actions>
          <v-btn color="accent" x-small depressed @click="handleClick">
            <div v-if="!collapseOnScroll">
              <v-icon small>mdi-arrow-right</v-icon>
            </div>
            <div v-else>
              <v-icon small>mdi-arrow-left</v-icon>
            </div>
          </v-btn>
        </v-card-actions>
      </v-app-bar>
      <v-sheet
        id="scrolling-techniques-6"
        class="overflow-y-auto"
        min-height="75"
      >
        <v-container style="height: auto"></v-container>
      </v-sheet>
      <v-expand-transition>
        <div v-show="collapseOnScroll">
          <v-card color="background">
            <v-row v-for="(item, i) in items" :key="i">
              <v-col>
                <v-card-title class="subheading font-weight-bold justify-left">
                  {{ item.name }}
                </v-card-title>

                <v-chip
                  class="ma-2"
                  v-for="(elem, j) in item.skills"
                  :key="j"
                  :color="j < number_top_skills ? 'accent' : 'default'"
                  medium
                >
                  {{ elem }}
                </v-chip>
              </v-col>
            </v-row>
          </v-card>
        </div>
      </v-expand-transition>
    </v-card>
  </v-container>
</template>



<script>
export default {
  data: () => ({
    number_top_skills: 3,
    items: [
      {
        name: "Skills",
        skills: [
          "Python",
          "Software Testing",
          "Gitlab",
          "Docker",
          "Podman",
          "AWS",
          "Kubernetes",
          "Crossplane",
          "Bash",
          "Terraform",
          "Git",
          "Ansible",
          "Powershell",
          "Vue.js",
          "Lektor",
          "Jinja2",
          "Groovy",
          "EXAM-Testautomation",
          "Enterprise Architect",
          "TeamScale",
          "Jenkins",
          "Bitbucket",
          "PostgreSQL",
          "Arduino C/C++",
          "Raspberry Pi",
          "ECU-TEST",
          "TEST-GUIDE",
          "LabVIEW",
          "Autodesk Fusion",
          "SolidWorks",
        ],
      },
      {
        name: "Hilfsmittel",
        skills: [
          "Scrum",
          "Agile Methoden",
          "Jira",
          "Confluence",
          "Sharepoint",
          "Figma",
          "Excel",
          "Matrix (Element)",
          "Powerpoint",
          "Request Tracker",
          "OKR",
        ],
      },
    ],

    collapseOnScroll: false,
  }),

  methods: {
    handleClick: function () {
      this.collapseOnScroll = !this.collapseOnScroll;
    },
  },
};
</script>

<style scoped>
.v-progress-circular {
  margin: 1rem;
}
</style>