        details: "",
      },
      {
        period: "09/2012 - 07/2013",
        school: "Vocational baccalaureate in technology",
        graduation: "BSZ Technik Gustav Anton Zeuner",
        link: "https://www.bsz-technik-zeuner.de/fachoberschule/",
        details: "",
      },
      {
        period: "09/2006 - 02/2010",
